* {
  box-sizing: border-box;
  font-family: "Noto Sans", sans-serif;
  text-decoration: none;
}

.wrapper {
  max-width: 790px;
  margin: 0 auto; 
}
.marginWrap {
  margin: 5px;
}

.vacCard {
  margin-bottom: 20px;
  display: grid;
  grid-gap: 10px;
  grid-template-areas:
    "vacn vacn "
    "vacRes vacs"
    "vacC vacC"
    "btn  logo ";
    
  min-height: 115px;
  padding: 10px;
  border: 1px solid #e7e7e7;
}

.vacName {
  display: flex;
  justify-content: flex-start;
  font-size: 17px;
  color: #4682b4;
  grid-area: vacn;
  margin-bottom: 3px;
}

.vacSalary {
  display: flex;
  justify-content: flex-end;
  color: #4682b4;
  grid-area: vacs;
}
.closePngwrapper {
  display: flex;
  justify-content: flex-end;
  width: 20px;
}  

.closepng:before,
.closepng:after {
  content: "";
  position: absolute;
  top: 14px;
  right:  21px;
  width: 20px;
  height: 4px;
  background: #4682b4;
}
.closepng:before {
  transform: rotate(45deg);
}

.closepng:after { 
  transform: rotate(-45deg);
}
.closepng:hover::after {
  width: 25px;
  background: #438dca;
}
.closepng:hover:before {
  background: #438dca;
  width: 25px;
}

.btn_wrapper {
  display: flex;
  justify-content: flex-start;
  grid-area: btn;
}
.btn_vacCard {
  background-color: #6633ff;
  color: #fff;
  border: 1px solid #bbb;
  width: 92 px;
  height: 31px;
  border-radius: 15px;
  text-align: center;
  cursor: pointer;
  outline: none;
}

.btn_vacCard:hover {
  background-color: #333399;
  color: #bbb;
}

.vacCity { 
  display: flex;
  justify-content: flex-start;
  color: #999;
  font-size: 16px;
  grid-area: vacC;
  margin-bottom: 4px;
}

@keyframes showModal {
  0% {
    transform: translateX(500px);
  }

  100% {
    transform: translateX(0px);
  }
}
.modal {
  position: fixed;
  height: 100vh;
  top: 0;
  right: 0;
  left: 0;
  bottom: 0;
  display: flex;
  justify-content: flex-end;
  align-items:flex-end ;
  background-color: rgba(0, 0, 0, 0.041); 
 
}

.modalContent {
  display: flex;
  padding: 10px;
  flex-direction: column;
  animation: showModal 0.3s ease-out;
  font-family: "Noto Sans", sans-serif;
  background: #ffffff;
  width: 600px;
  height:100%;
  box-shadow: 0 0 25px rgba(0, 0, 0, 0.144);
  border-radius: 10px;
  color: #444;
  overflow-y: scroll;
} 
.vacInfo{
  margin-top: 5px;
  
}

.modalTitle {
  margin-top: 20px;
  text-align: center;
  font-size: 20px;
  margin-bottom: 50px;
  border-bottom: 1px solid #4682b4;
}

.imLogo {
  max-width: 60;
  max-height: 30px;
}
.logo {
  grid-area: logo;
  display: flex;
  justify-content: flex-end;
}

.logoModal {
  max-width: 70px;
}

.btn {
  color: blue;
}
nav {
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
}
ul.pagination {
  display: flex;
  padding-inline-start: 0;
}
li {
  list-style: none;
  outline: none;
  border-radius: 3px;
  width: 20px;
  text-align: center;
  cursor: pointer;
  margin-right: 4px;
  user-select: none;
  color: #4682b4;
  background: #fefefe;
}
li:hover {
  background: #bbb;
}

li.active {
  color: #4682b4;
  background: #e9e9e9;
}
.prev,
.next {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 20px;
  border-radius: 3px;
  max-height: 20px;
  margin-left: 6px;
  margin-right: 6px;
  cursor: pointer;
  color: #4682b4;
  background: #fff;
  user-select: none;
}

.next:hover {
  background: #bbb;
}
.prev:hover {
  background: #bbb;
}
  .logomodal {
    margin-top: 20px;
  }
 .iamLogomodal {
   max-width: 100px;
   max-height: 60px;
 }

.vacResp{
  color: #333;
  font-size: 16px;
  margin-top: 4px;
  grid-area:vacRes;
}

.phoneNum, .email {
  outline: none;
  display: block;
  width: 56%;
  padding: 0 20px;
  margin-bottom: 10px;
  line-height: 40px;
  background: #E9EFF6;
  border-width: 0;
  border-radius: 20px;
  
}

.submit{
  display: flex;
  flex-direction: column;
  align-items: center;
}

.submitBtn{
  background-color: #6633ff;
  color: #fff;
  border: 1px solid #bbb;
  max-width: 135px;
  height: 31px;
  border-radius: 15px;
  text-align: center;
  cursor: pointer;
  outline: none;
}

.subm{
  margin-top: 70px;
  font-size: 18px;
  margin-bottom: 14px;
}

.submitBtn:hover {
  background-color: #333399;
  color: #bbb;
}
